<template>
  <div class="search" v-show="isShowHtml">
    <!-- 搜索栏 -->
    <div>
      <form action="/">
        <van-search
          v-model="value"
          show-action
          placeholder="请输入搜索关键词"
          @search="onSearch"
          @cancel="onCancel"
        />
      </form>
    </div>
    <!-- 内容筛选栏 -->
    <div class="classify-box"></div>
    <!-- 查询结果栏 -->
    <div v-if="listData.length !== 0" class="list-box">
      <div
        class="list-item"
        v-for="(listItem,listIndex) in listData"
        :key="listIndex"
        @click="goIndetaild(listItem.id)"
      >
        <div class="img-box">
          <img v-lazy="listItem.vertical_cover" />
        </div>
        <div class="title-box">
          <span class="title" v-html="listItem.title"></span>
          <div class="text">
            <span
              class="auter"
              v-for="(auterItem,auterIndex) in listItem.author_name"
              :key="auterIndex"
            >{{auterItem}}</span>
            <p>{{listItem.styles[0]}}</p>
            <p v-if="listItem.is_finish === 0">连载中</p>
            <p v-else>已完结</p>
          </div>
        </div>
      </div>
    </div>
    <div v-else class="noDataImg">
      <img src="../assets/bea10bfe88465.png" alt />
      <div class="text2233">什么都没有 ( ´･_･)ﾉ(._.`)</div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Search",
  data() {
    return {
      value: "",
      listData: [],
      isShowHtml: false
    };
  },
  created() {
    this.value = this.$route.params.val;
    this.getSearch(this.value);
  },
  methods: {
    goIndetaild(id) {
      this.$router.push({
        name: "Indetail",
        params: {
          id
        }
      });
    },
    onSearch(val) {
      this.$router.push({
        name: "Search",
        params: {
          val
        }
      });
      this.getSearch(val);
    },
    onCancel() {
      this.listData = [];
      this.$router.push({
        name: "Home"
      });
    },

    getSearch(val) {
      this.axios({
        method: "get",
        url: "/Search",
        params: {
          keyWord: val,
          styleId: -1,
          areaId: -1,
          isFinish: -1,
          order: 3,
          pageNum: 1,
          pageSize: 20,
          isFree: -1
        }
      })
        .then(res => {
          //  
          this.listData = res.data.data.list;
          
          this.isShowHtml = true;
        })
        .catch(err => {
          
        });
    }
  }
};
</script>

<style lang="scss" scoped>
.search {
  height: 150px;
  .list-box {
    position: fixed;
    top: 60px;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    overflow-y: scroll;
    padding: 10px;
    .list-item {
      display: flex;
      margin-bottom: 20px;
      .img-box {
        height: auto;
        img {
          width: 150px;
        }
      }
      .title-box {
        padding: 10px;
        overflow: hidden;
        white-space: nowrap;
        text-overflow: ellipsis;
        .title {
          font-weight: bold;
          font-size: 16px;
        }
        .text {
          width: 100%;
          font-size: 15px;
          margin-top: 80px;
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
          .auter {
            margin: 0px 3px;
          }
          p {
            margin: 5px 0;
          }
        }
      }
    }
  }
  .noDataImg {
    width: 100%;
   //  display: flex;
   //  justify-content: center;
   //  align-items: center;
    img {
      width: 100%;
    }
    .text2233{
      text-align: center;
      font-size: 18px;
    color: #999;
    }
  }
}
</style>